body {
  background-color: #315468;
}

.App {
  background-color: #322e3f;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
  border-radius: 6px;
}

#to-do-form h1 {
  color: #fff;
  font-size: 20px;
  margin: auto;
  padding-top: 10px;
  text-align: center;
}

#to-do-form input {
  background-color: #45b39d;
  border: 0;
  width: 230px;
  height: 50px;
  padding: 0 10px;
  margin: 15px;
  margin-left: 38px;
  font-size: 18px;
  border-radius: 6px;
  color: #fff;
  transition: 0.6s;
}

#to-do-form input::placeholder {
  color: rgb(211, 209, 209);
}

#to-do-form input:focus {
  margin: 15px;
  width: 275px;
  transition: 0.6s;
  outline: none;
}

#to-do-form button {
  height: 48px;
  width: 60px;
  border-radius: 5px;
  border: 0;
  background-color: #5dade2;
  cursor: pointer;
  font-weight: bolder;
  color: #fff;
  outline: none;
}
