.list {
  color: #ffffff;
  width: 300px;
  background-color: #28b463;
  height: 50px;
  margin: 20px auto;
  border-radius: 6px;
}

.list p {
  padding: 14px 10px;
  position: relative;
}

.list p span {
  position: absolute;
  right: 10px;
  cursor: pointer;
}

.list p input {
  background-color: transparent;
  border: 0;
  color: #ffffff;
  font-size: 18px;
}

.list p input:focus {
  outline: none;
}

.faicons {
  margin: 0 5px;
  font-size: 15px;
}
